<template>
  <v-container>
    <v-card v-if="profile" class="pa-5">
      <v-card-text>
        <v-simple-table>
          <tbody>
            <tr v-for="(p, i) in profile" :key="i">
              <td>{{ i }}</td>
              <td>{{ p }}</td>
            </tr>
          </tbody>
        </v-simple-table>
      </v-card-text>
    </v-card>
    <v-skeleton-loader
      v-else
      v-bind="attrs"
      type="card-avatar, article, actions"
    ></v-skeleton-loader>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      profile: null,
    };
  },
  methods: {
    getProfile() {
      fetch(
        `https://script.google.com/macros/s/AKfycbzFgqSH8kZIIIKtH6C7P7TzEMfJgXoiNP2VdCZf7n_hLxyq3OLFMEUtuBSgIY3agt7i/exec?sheet=${this.user.photoURL}&type=Initial Data`
      )
        .then((res) => res.json())
        .then((data) => {
          this.profile = data.routines[0];
        });
    },
  },
  computed: {
    user() {
      return this.$store.getters.getUser;
    },
  },
  created() {
    this.getProfile();
  },
};
</script>

<style lang="scss" scoped>
</style>